import { Fragment, useState } from 'react'
//import { Label } from 'reactstrap'
import Flatpickr from 'react-flatpickr'
import '@styles/react/libs/flatpickr/flatpickr.scss'
import { getDate } from './store/action'
import { useDispatch } from 'react-redux'

const PickerRange = () => {
  const [picker, setPicker] = useState(new Date())
  const dispatch = useDispatch()
  console.log(picker)
  const formatDate = (value, formatting = { month: 'short', day: 'numeric', year: 'numeric' }) => {
    if (!value) return value
    return new Intl.DateTimeFormat('en-US', formatting).format(new Date(value))
  }
  
  console.log(picker)
  console.log(`from:${picker[0]} to:${picker[1]}`)
  console.log(formatDate(picker[0] === undefined ? 0 : picker[0]))
  console.log(formatDate(picker[1] === undefined ? 0 : picker[1]))

  const dater = (date) => {
    setPicker(date)
    const from = formatDate(picker[0] === undefined ? 0 : picker[0])
    const to = formatDate(picker[1] === undefined ? 0 : picker[1])
    dispatch(getDate(from, to))
  }
  return (
    <Fragment>
    
      <Flatpickr
        value={picker}
        id="range-picker"
        className="form-control"
        onChange={(date) => dater(date)}
        options={{
          mode: 'range',
          defaultDate: ['2020-02-02', '2020-02-15']
        }}
      />
    </Fragment>
  )
}

export default PickerRange
