import { Fragment, lazy, useState, Suspense } from 'react'
import { Row, Col, ButtonGroup, Button } from 'reactstrap'
import PickerRange from "./PickerRange"
// import Breadcrumbs from '@components/breadcrumbs'
import { Users, Dribbble } from 'react-feather'

const StatisticsCards = () => {
  const [selectedStat, setSelectedStat] = useState(0)

  const Content =
    selectedStat === 0
      ? lazy(() => import('./UserStats'))
      : lazy(() => import('./SportsStats'))

  return (
    <Fragment>
      {/* <Breadcrumbs breadCrumbTitle='Statistics Cards' breadCrumbParent='Card' breadCrumbActive='Statistics Cards' /> */}
      <Row>
        <Col md={8} sm={12}>
          <ButtonGroup className="mb-1">
            <Button
              outline
              color="primary"
              onClick={() => setSelectedStat(0)}
              active={selectedStat === 0}
            >
              &ensp;&emsp;&ensp;&emsp;
              <Users size={14} />
              <span className="align-middle ml-25">User Stats</span>
              &ensp;&emsp;&ensp;&emsp;
            </Button>
            <Button
              outline
              color="primary"
              onClick={() => setSelectedStat(1)}
              active={selectedStat === 1}
            >
              &ensp;&emsp;&ensp;&emsp;
              <Dribbble size={14} />
              <span className="align-middle ml-25">Sports Stats</span>
              &ensp;&emsp;&ensp;&emsp;
            </Button>
           
          </ButtonGroup>
        </Col>
        
        <Col md={3}>
          <PickerRange />
        </Col>
       
      </Row>
      <Suspense>
       <Content />
      </Suspense>
    </Fragment>
  )
}

export default StatisticsCards
